#container {
    display: flex;
    width: 100vw;
    height: 100vh;
    margin-bottom: 20%;
    padding-top: 20px;
    justify-content: center;
}

.containe {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
}


@media (max-width: 1006px) {
    .containe {
        display: flex;
        flex-wrap: wrap;
        height: -webkit-max-content;
        height: max-content;
        flex-direction: column;
    }
}

#container .card1 {
    position: relative;
    width: 350px;
    height: 420px;
    background: #fff;
    margin: 50px 10px;
    padding: 20px 15px;
    display: flex;
    flex-direction: column;
    box-shadow: 0 5px 20px rgba(0, 0, 0, 0.5);
}

#container .card1:hover .imgBx {
    translate: 0 -50px 0;
}

#container .card1 .imgBx {
    position: relative;
    width: 320px;
    height: 260px;
    top: -5px;
    z-index: 1;
    box-shadow: 0 5px 20px rgba(0, 0, 0, 0.2);
    transition: 0.3s ease-in-out;
}

#container .card1 .imgBx img {
    height: 260px;
    width: 320px;
}

#container .card1 .content1 {
    position: relative;
    margin-top: -140px;
    padding: 10px 15px;
    text-align: center;
    color: #000;
    visibility: hidden;
    opacity: 0;
    transition: 0.3s ease-in-out;
}

#container .card1 .content2 {
    position: absolute;
    height: 40px;
    width: 100%;
    top: 420px;
    left: -1px;
    background: #333;
    box-shadow: 0 5px 20px rgba(0, 0, 0, 0.5);
}

#container .card1 .content2 p {
    margin-top: -5px;
}

#container .card1:hover .content1 {
    visibility: visible;
    opacity: 1;
    margin-top: -40px;
}
